<template>
  <div class="boradWrap">
    <template v-if="!board_modify">
      <div class="listWrap">
        <el-button
          @click="$router.push({ name: 'boardList', query: { page: 1 } })"
          icon="el-icon-s-fold"
          size="mini"
        >
          
        </el-button>
      </div>
      <div class="titleWrap"
        ><h2>{{ board.title }}</h2></div
      >
      <div class="contentWrap"
        ><p>{{ board.content }}</p></div
      >
      <div v-if="!!board.attachment" class="attachmentWrap"
        ><el-button
          v-for="file in board.attachment"
          :key="file._id"
          type="text"
          size="mini"
          icon="el-icon-folder"
          @click="fileDownload(file.serverFileName)"
          >{{ file.originalFileName }}</el-button
        ></div
      >
      <div v-if="current_user.email === board.uploadedBy.email" class="btnWrap">
        <el-button size="mini" @click="board_modify = true">  </el-button>
        <el-button size="mini" @click="deleteBoard()">  </el-button>
      </div>
      <div class="line"></div>
      <Comment :comments="comments" @callFecthBoard="fecthBoard"></Comment>
    </template>
    <!--   -->
    <template v-else>
      <h1></h1>
      <div class="inputWrap">
        <p></p>
        <el-input placeholder="title" v-model="board.title" clearable>
        </el-input>
      </div>
      <div class="inputWrap">
        <p></p>
        <el-input placeholder="content" v-model="board.content" clearable>
        </el-input>
      </div>
      <div class="inputWrap">
        <p> </p>
        <el-button
          native-type="button"
          round
          icon="el-icon-document-add"
          size="mini"
          @click="handleFileUpload"
        ></el-button>
        <input
          type="file"
          id="userImage"
          ref="file"
          @change="previewImage"
          multiple
        />
        <transition name="slide-fade">
          <ul v-if="files.length" class="fileListWrap">
            <li
              class="fileList"
              v-for="(file, index) in this.files"
              :key="index"
            >
              <span @click="deleteFileList(index)"
                ><i class="el-icon-circle-close"></i></span
              >{{ file.name }}
            </li>
          </ul>
        </transition>
      </div>
      <div class="btnWrap">
        <el-button size="mini" @click="cancelModify()"></el-button>
        <el-button size="mini" @click="updateBoard()"></el-button>
      </div>
    </template>
  </div>
</template>

<script>
import { getBoard, deleteBoard, updateBoard } from '@/apis/board'
// import { downloadFile } from '@/apis/file'
import { mapState } from 'vuex'
import map from 'lodash/map'
import Comment from '@/components/comment'
export default {
  components: { Comment },
  data() {
    return {
      board: {
        attachment: {},
        uploadedBy: {
          email: '',
        },
      },
      comments: [],
      board_modify: false,
      visible: false,
      replay_visible: false,
      files: [],
    }
  },
  computed: {
    ...mapState('auth', ['current_user']),
  },
  methods: {
    async fecthBoard() {
      this.$store.dispatch('common/getLoading', true)
      const { data } = await getBoard(this.$route.params.id)
      this.board = data

      //  modify, reply  
      this.comments = map(this.board.comments, (el) => {
        return {
          ...el,
          modify: false,
          reply: false,
          visible: false,
          comments: map(el.comments, (reply) => {
            return { ...reply, modify: false }
          }),
        }
      })

      this.$store.dispatch('common/getLoading', false)
    },
    async deleteBoard() {
      await deleteBoard({ id: this.$route.params.id })
        .then(() => {
          this.$message({
            showClose: true,
            message: ' .',
            tpye: 'success',
          })
          this.$router.push({ name: 'boardList', query: { page: 1 } })
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.response.data.errors.message,
            tpye: 'error',
          })
        })
    },
    async updateBoard() {
      const formData = new FormData()
      formData.append('_id', this.board._id)
      formData.append('title', this.board.title)
      formData.append('content', this.board.content)
      formData.append('email', this.board.uploadedBy.email)
      if (this.file) {
        formData.append('attachment', this.file.raw, this.file.name)
      }
      if (this.board.attachment) {
        formData.append('attachment_id', this.board.attachment._id)
      }
      await updateBoard(formData)

      this.board_modify = false
      this.fecthBoard()
    },
    async fileDownload(key) {
      window.open(`http://localhost:3000/api/file/${key}`)
    },
    uploadFile(file) {
      this.file = file
    },
    cancelModify() {
      this.board_modify = false
      this.fecthBoard()
    },
    handleFileUpload() {
      this.$refs.file.click()
    },
    previewImage(e) {
      if (e.target.files.length > 3) {
        return this.$message({
          type: 'info',
          message: '    3 .',
          showClose: true,
        })
      }
      this.files = [...e.target.files]
    },
    deleteFileList(index) {
      this.files.splice(index, 1)
    },
  },
  mounted() {
    this.fecthBoard()
  },
}
</script>
<style lang="scss" scoped>
.listWrap {
  display: flex;
}
.attachmentWrap {
  margin-bottom: 10px;
}
</style>
